import Vue from 'vue'
import Router from 'vue-router'

// 懒路由加载
const Main = r => require.ensure([], () => r(require('@/views/modules/home/main')), 'main')
const Home = r => require.ensure([], () => r(require('@/views/modules/home/home')), 'home')
const Lottery = r => require.ensure([], () => r(require('@/views/modules/home/lottery')), 'lottery')
const Trend = r => require.ensure([], () => r(require('@/views/modules/home/trend')), 'trend')
const My = r => require.ensure([], () => r(require('@/views/modules/home/my')), 'my')

// 选号
const Nums = r => require.ensure([], () => r(require('@/views/modules/bet/numSelect')), 'numSelect')
const Confirm = r => require.ensure([], () => r(require('@/views/modules/bet/confirm')), 'confirm')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      redirect: 'home'
    },
    {
      path: '/',
      name: 'Main',
      component: Main,
      children: [
        {
          name: 'home',
          path: '/home',
          meta: {
            index: 1
          },
          component: Home
        },
        {
          name: 'lottery',
          path: '/lottery',
          meta: {
            index: 1
          },
          component: Lottery
        },
        {
          name: 'trend',
          path: '/trend',
          meta: {
            index: 1
          },
          component: Trend
        },
        {
          name: 'my',
          path: '/my',
          meta: {
            index: 1
          },
          component: My
        }
      ]
    },
    {
      name: 'numSelect',
      path: '/numSelect',
      meta: {
        index: 2
      },
      component: Nums
    },
    {
      name: 'confirm',
      path: '/confirm',
      meta: {
        index: 3
      },
      component: Confirm
    }
  ]
})
